body {
  display: block;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
}
.container {
  margin: 0 auto;
  padding: 0 30px;
  width: 260px;
  background: #ffffff;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-weight: normal;
  font-family: "Open Sans";
  font-style: normal;
  src: url("fonts/opensans.woff2") format("woff2"), url("fonts/opensans.woff") format("woff");
}
@font-face {
  font-weight: bold;
  font-family: "Open Sans";
  font-style: normal;
  src: url("fonts/opensansbold.woff2") format("woff2"), url("fonts/opensansbold.woff") format("woff");
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
    width: calc(768px - 100px);
  }
}
.page-header {
  margin: 0 -30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-top: 1px solid #ececec;
  border-bottom: 1px solid #ececec;
}
.page-header__top-menu {
  display: flex;
  flex-direction: row;
}
.page-header__logo {
  width: 240px;
  height: 35px;
  padding: 22px 0px;
  display: flex;
}
.page-header__logo svg {
  margin: 0 0 0 48%;
  display: block;
}
.page-header__logo svg:hover {
  fill: #666666;
}
.page-header__logo svg:active {
  fill: #b2b2b2;
}
.page-header__nav-toggle {
  border: none;
  background: #000000;
  display: flex;
  width: 20px;
  height: 2px;
  margin: 38px 31px 28px 29px;
}
.page-header__nav-toggle::before {
  display: block;
  width: 20px;
  height: 2px;
  background: #000000;
}
.site-list {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
.site-list__item {
  border-bottom: 1px solid #ececec;
}
.site-list__item:last-child {
  border: none;
}
.site-list__item a {
  display: flex;
  padding: 26px 0 28px 73px;
  text-decoration: none;
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
  color: #000000;
  cursor: pointer;
}
.site-list__search path,
.site-list__cart path {
  fill: #d3d2d2;
}
.site-list__search svg {
  margin-left: -42px;
  margin-top: 7px;
  position: absolute;
}
.site-list__cart svg {
  margin-left: -44px;
  margin-top: 5px;
  position: absolute;
}
.page-main__headline {
  margin: 0 -30px;
  color: #010000;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 27px;
  font-weight: 700;
  line-height: 30px;
  text-align: center;
  background: #f1f1f1;
  padding: 70px 0 75px;
}
.page-main__headline--index {
  padding: 70px 30px 122px;
  background: url(../img/hero-mobile@1x.jpg) #cddedc;
  background-position: -127px 355px;
}
.page-main__form-desctription {
  font-weight: normal;
  font-size: 13px;
  line-height: 24px;
  margin: 22px 0 5px;
}
.page-footer {
  margin: 0 -30px;
  display: flex;
  flex-direction: column;
  min-height: 160px;
  background: #f1f1f1;
}
.page-footer__logo {
  display: none;
}
@media (min-width: 768px) {
  .page-footer__logo {
    display: flex;
    height: 35px;
    margin: 51px 142px 0 50px;
  }
  .page-footer__logo svg:hover {
    fill: #666666;
  }
  .page-footer__logo svg:active {
    fill: #b2b2b2;
  }
  .page-footer {
    margin: 0 -50px;
    flex-direction: row;
    min-height: 140px;
  }
}
.merchandise {
  list-style: none;
  margin: 0 -30px;
  padding: 0;
}
.merchandise__item {
  display: flex;
  flex-direction: row;
  height: 103px;
}
.merchandise__item:hover {
  height: 108px;
}
.merchandise__item a {
  display: flex;
  padding: 24px 0 31px 30px;
  width: 160px;
  font-size: 19px;
  font-weight: 700;
  line-height: 24px;
  color: white;
  text-decoration: none;
}
.merchandise__lamp {
  background: #63d1bb;
}
.merchandise__lamp:hover {
  background: #59d5bc;
}
.merchandise__lamp:active a {
  color: #97e2d2;
}
.merchandise__lamp:active svg {
  fill: #44c0aa;
}
.merchandise__lamp svg {
  padding: 22px 0 0px 53px;
  fill: #3bb09d;
}
.merchandise__brick {
  background: #56c3b2;
}
.merchandise__brick:hover {
  background: #46c1ae;
}
.merchandise__brick:active a {
  color: #8dd5c9;
}
.merchandise__brick:active svg {
  fill: #37b2a0;
}
.merchandise__brick svg {
  padding: 21px 0 0px 43px;
  fill: #35a999;
}
.item {
  display: flex;
  flex-direction: column;
  width: 260px;
  padding: 27px 0 50px;
  background: url(../img/bg-zigzag-line.svg) 10px 820px repeat-x;
  background-size: auto 6px;
}
.item__headline {
  font-size: 25px;
  line-height: 30px;
  margin: 0 0 5px;
}
.item__weekly {
  color: #63d1bb;
  font-size: 17px;
  line-height: 20px;
  font-weight: bold;
  text-transform: uppercase;
  margin: 0;
}
.item__description {
  font-size: 15px;
  line-height: 24px;
  color: #444444;
  margin: 24px 0 0;
}
.item__features {
  margin: 15px 0 0;
  font-size: 15px;
  line-height: 20px;
  color: #444444;
  display: flex;
  flex-direction: column;
}
.features__line {
  display: flex;
  justify-content: flex-start;
  padding: 10px 0 8px;
  border-bottom: 1px solid #e5e5e5;
}
.features__line td {
  min-width: 95px;
}
.item__photo {
  margin: 27px 0 0;
}
.item__price {
  font-size: 17px;
  line-height: 24px;
  color: #444444;
  font-weight: bold;
  margin: 24px auto 0;
}
.item__buy {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 17px;
  line-height: 24px;
  color: #444444;
  font-weight: bold;
  padding: 12px 83px;
  border: 2px solid #444444;
  margin: 26px 0 0;
}
.item__buy:hover {
  background: #444444;
  color: white;
}
.item__buy:active {
  color: #8d8d8d;
}
.about {
  flex-direction: column;
  width: 260px;
  padding: 20px 0 32px;
}
.about__headline {
  font-size: 25px;
  line-height: 30px;
  font-weight: bold;
  display: flex;
  margin: 0;
}
.about__line {
  font-size: 17px;
  line-height: 20px;
  font-weight: bold;
  padding: 83px 0 0;
  margin: 0;
  width: 250px;
}
.reviews {
  display: flex;
  flex-direction: column;
  background: url(../img/bg-zigzag-line.svg) 10px 650px repeat-x;
  background-size: auto 6px;
  min-height: 655px;
}
.reviews__headline {
  font-size: 25px;
  line-height: 30px;
  font-weight: bold;
  display: flex;
}
.reviews__message {
  font-size: 15px;
  line-height: 30px;
  color: #444444;
  margin: 0;
}
.reviews__author {
  margin: 23px 0 10px;
  font-size: 17px;
  line-height: 30px;
  color: #444444;
}
.reviews__author-name {
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
}
.reviews__contact {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 20px;
  color: #444444;
  font-weight: bold;
  padding: 14px 0;
  text-align: center;
  border: 2px solid #444444;
  margin: 26px 0 0;
}
.reviews__contact:hover {
  background: #444444;
  color: white;
}
.reviews__contact:active {
  color: #8d8d8d;
}
.reviews__page {
  display: flex;
  flex-direction: row;
  margin: 20px 0 10px;
}
.reviews__change-page--prev,
.reviews__change-page--next {
  max-width: 50px;
  border: 1px solid #ececec;
  padding: 12px 42px 7px 37px;
  margin: 0;
}
.reviews__change-page--prev:hover,
.reviews__change-page--next:hover {
  fill: #666666;
}
.reviews__change-page--prev:active,
.reviews__change-page--next:active {
  fill: #b2b2b2;
}
.reviews__change-page--prev:last-child,
.reviews__change-page--next:last-child {
  border-left: none;
}
.contacts {
  display: flex;
  flex-direction: column;
  width: 260px;
  padding: 35px 0 0;
}
.contacts__headline {
  font-size: 18px;
  line-height: 30px;
  font-weight: bold;
  display: flex;
  margin: 0 0 25px;
}
.contacts__data {
  font-size: 15px;
  line-height: 30px;
  display: flex;
  margin: 0 0 8px;
  flex-direction: row;
  color: #444444;
}
.contacts__data tr {
  display: flex;
}
.contacts__data td {
  padding: 0;
  margin: 0 0 30px;
  min-width: 78px;
  vertical-align: top;
}
.contacts__mail td:nth-child(2) {
  border-bottom: 1px solid #62d1ba;
}
.contacts__mail td:nth-child(2):hover {
  color: #46c1ae;
  border-color: #46c1ae;
}
.contacts__mail td:nth-child(2):active {
  color: #ecf9f7;
  border-color: #ecf9f7;
}
.contacts__map {
  display: flex;
  margin: 0 -30px;
  width: 360px;
}
.contacts__contact {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 20px;
  color: #444444;
  font-weight: bold;
  padding: 14px 0;
  text-align: center;
  border: 2px solid #444444;
  margin: 38px 0 39px;
}
.contacts__contact:hover {
  background: #444444;
  color: white;
}
.contacts__contact:active {
  color: #8d8d8d;
}
.social {
  display: flex;
  flex-direction: row;
  padding: 0 0 0 40px;
  margin: 0 auto;
  height: 80px;
  width: 68.75%;
  border-bottom: 1px solid #e5e5e5;
  list-style: none;
}
.social__item {
  display: flex;
  width: 60px;
  margin: auto 0;
}
.social__link {
  margin: 0 auto;
}
.social__link:hover svg {
  fill: #46c1ae;
}
.social__link:active svg {
  fill: #bde3dd;
}
@media (min-width: 768px) {
  .social {
    width: 180px;
    border: none;
    margin: 51px auto;
    padding: 0;
    height: auto;
  }
  .social__item {
    margin: auto;
  }
}
.page-footer__copyright {
  color: #000000;
  text-decoration: none;
  display: flex;
  flex-direction: row;
  font-weight: bold;
  font-size: 19px;
  width: 56.25%;
  margin: 22px auto 36px;
}
.page-footer__copyright:hover svg {
  fill: #46c1ae;
}
.page-footer__copyright:active {
  color: #bfbfbf;
}
.page-footer__copyright:active svg {
  fill: #bde3dd;
}
.page-footer__copyright svg {
  padding: 0 0 0 26px;
}
@media (min-width: 768px) {
  .page-footer__copyright {
    margin: 56px 50px 51px 62px;
  }
}
.catalog-item {
  display: flex;
  flex-direction: column;
  margin: 30px 0 0;
  border: 1px solid #ededed;
}
.catalog-item__link {
  text-decoration: none;
}
.catalog-item__headline {
  margin: 22px auto 6px;
  width: 200px;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
}
.catalog-item__description {
  font-weight: normal;
  font-size: 13px;
  line-height: 30px;
  margin: 0 0 0 27px;
  padding: 0 0 18px;
  width: 200px;
  border-bottom: 1px solid #ececec;
}
.catalog-item__price {
  font-weight: bold;
  font-size: 15px;
  line-height: 24px;
  padding: 7px 0 12px 28px;
}
.catalog-item__buy {
  margin: 0px;
  padding: 0px;
}
.catalog-item__buy path {
  fill: #d9d9d9;
}
.catalog-making {
  display: flex;
  flex-direction: column;
  margin: 30px 0 58px;
  border: 1px solid #ececec;
}
.catalog-making__headline-container {
  margin: 0;
  padding: 0;
}
.catalog-making__headline {
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  margin: 22px 0 24px;
  padding: 0 0 0 90px;
  /*потом убрать и занять место svgшкой*/
}
.catalog-making__video {
  margin: 0;
  padding: 0;
  background: url(../img/video-mobile@1x.jpg) #7dcaba;
}
.catalog-making__description {
  margin: 27px 0 0 33px;
  width: 190px;
  font-weight: normal;
  font-size: 13px;
  line-height: 24px;
}
.catalog-making__buy {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 17px;
  line-height: 24px;
  color: #444444;
  font-weight: bold;
  padding: 12px 24px;
  border: 2px solid #444444;
  margin: 26px 33px 31px;
}
.catalog-making__buy:hover {
  background: #444444;
  color: white;
}
.catalog-making__buy:active {
  color: #8d8d8d;
}
.form {
  display: flex;
  flex-direction: column;
}
.form__heading {
  font-size: 17px;
  line-height: 16px;
  font-weight: bold;
  color: #63d1bb;
  text-transform: uppercase;
  width: 60px;
  height: 12px;
  margin: 20px 0 15px;
}
.form__heading::after {
  background-color: #63d1bb;
  width: 200px;
  height: 1px;
  display: block;
  content: '';
  margin: -8px 0 0 60px;
}
.form__heading--phone {
  margin: 30px 0 10px;
}
.form__heading--mail {
  margin: 32px 0 10px;
}
.form__heading--mail::after {
  margin: -8px 0 0 80px;
  width: 180px;
}
.form__heading--additional {
  margin: 32px 0 30px;
}
.form__option {
  min-height: 40px;
  padding: 22px 0 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  font-size: 17px;
  line-height: 18px;
  font-weight: bold;
}
.form__option input {
  margin: 0;
  min-width: 62px;
}
.form__name {
  display: flex;
  flex-direction: column;
  height: 75px;
  padding: 0;
  margin: 16px 0 0;
}
.form input[type="text"],
.form input[type="tel"],
.form input[type="email"] {
  font-size: 17px;
  line-height: 18px;
  font-weight: normal;
  color: #444444;
  border: none;
  border-bottom: 2px solid #000000;
  padding: 18px 0 15px;
}
.form__wishes {
  vertical-align: top;
  font-size: 17px;
  line-height: 18px;
  font-weight: normal;
  color: #444444;
  border: 2px solid #000000;
  padding: 17px 15px;
  height: 105px;
  resize: none;
}
.form__comment {
  font-size: 14px;
  line-height: 16px;
  font-weight: normal;
  color: #444444;
  margin: 15px 0 30px;
}
.form__submit {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 17px;
  line-height: 24px;
  color: #444444;
  font-weight: bold;
  padding: 12px 24px;
  border: 2px solid #444444;
  margin: 0 0 56px;
  background: #ffffff;
}
.form__submit:hover {
  background: #444444;
  color: white;
}
.form__submit:active {
  color: #8d8d8d;
}
